* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
}

body {
  color: #333;
  background-color: #f8f9fa;
  line-height: 1.6;
}

.container {
  text-align: center;
  max-width: 800px;
  margin: 0 auto;
  padding: 2rem;
}

h1 {
  color: #333;
  margin-bottom: 2rem;
  font-size: 2.8rem;
  font-weight: 800;
}

h2 {
  margin-bottom: .5rem;
  font-size: 1.5rem;
}

.time-display {
  flex-wrap: wrap;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 2rem;
  display: flex;
}

.time-box {
  background-color: #fff;
  border-radius: 12px;
  flex: 1;
  min-width: 280px;
  max-width: 380px;
  margin: .5rem;
  padding: 1.5rem;
  transition: transform .2s;
  box-shadow: 0 6px 15px #00000014;
}

.time-box:hover {
  transform: translateY(-5px);
}

.time {
  color: #444;
  margin-top: .5rem;
  font-size: 1.6rem;
  font-weight: bold;
}

.status-container {
  border-radius: 12px;
  max-width: 600px;
  margin: 2rem auto;
  padding: 2.5rem;
  transition: transform .2s;
  box-shadow: 0 6px 15px #00000014;
}

.status-container:hover {
  transform: translateY(-5px);
}

.status {
  color: #fff;
  margin-bottom: 0;
  font-size: 2.4rem;
  font-weight: 800;
}

.working {
  background-color: #dc3545;
  background-image: linear-gradient(135deg, #e04c59 0%, #c62a39 100%);
}

.not-working {
  background-color: #28a745;
  background-image: linear-gradient(135deg, #34ce57 0%, #218838 100%);
}

.time-until {
  margin: 2rem 0;
  font-size: 1.5rem;
  line-height: 1.8;
}

.graph-container {
  background-color: #fff;
  border-radius: 12px;
  max-width: 740px;
  margin: 2rem auto;
  padding: 1.5rem;
  transition: transform .2s;
  box-shadow: 0 6px 15px #00000014;
}

.graph-container:hover {
  transform: translateY(-5px);
}

.graph-description {
  color: #6c757d;
  margin-bottom: 1rem;
}

#llm-load-graph {
  max-width: 100%;
  height: auto;
  margin: 0 auto;
  display: block;
}

footer {
  color: #6c757d;
  margin-top: 3rem;
  font-size: .9rem;
}

@media (width <= 600px) {
  .container {
    padding: 1rem;
  }

  h1 {
    font-size: 2rem;
  }

  .status {
    font-size: 1.5rem;
  }

  #llm-load-graph {
    height: 200px;
  }
}
